import React, { useState, useEffect } from "react";
import {
  Flex,
  useColorMode,
  Box,
  Stack,
  HStack,
  VStack,
  Text,
  Center,
  Divider,
  Spacer,
  Icon,
  useMediaQuery,
  Button,
  Input,
  NumberInput,
  NumberInputField,
  InputRightElement,
  InputGroup,
  Image,
  useClipboard,
  useToast,
  useColorModeValue
} from "@chakra-ui/react";
import { BsGear, BsCheckAll } from "react-icons/bs";
import {
  useEthers,
  useEtherBalance,
  useTokenBalance,
  useSendTransaction,
  useContractFunction,
} from "@usedapp/core";
import BNBLogo from "../img/bnb.svg";
import ToTheMarsLogo from "../img/logotothemars.svg";
import { formatEther, formatUnits } from "@ethersproject/units";
import {
  ToTheMarsMainnet,
  rate,
  MartianSwapMainnet,
  DevWallet,
} from "../contracts/ContractAddress";
import { ethers, utils } from "ethers";
import { abi } from "../contracts/abi/MartianSwapReferral.json";
import { Contract } from "@usedapp/core/node_modules/ethers";
import {
  Price,
  GetTime,
  Owner,
  RemainingToken,
  TokenAddress,
  useContractMethod,
  ifReferrer,
} from "../contracts/hooks/MartianSwapReferralHooks";

function MartianSwap() {
  const [isPC] = useMediaQuery("(min-width: 750px)");
  const { colorMode } = useColorMode();
  const { activateBrowserWallet, account, chainId, active } = useEthers();
  const FetchBNBBalance = useEtherBalance(account);
  const BNBBalance =
    FetchBNBBalance && parseFloat(formatEther(FetchBNBBalance)).toFixed(5);
  const { sendTransaction, state } = useSendTransaction();
  const FetchToTheMarsBalance = useTokenBalance(ToTheMarsMainnet, account);
  const ToTheMarsBalance =
    FetchToTheMarsBalance &&
    parseFloat(formatUnits(FetchToTheMarsBalance)).toFixed(5);

  const [disabled, setDisabled] = useState(false);

  const [bnbValue, setBnbValue] = useState(0);
  const [martianValue, setMartianValue] = useState(0);
 
  const toast = useToast();

  const MartianSwapInterface = new utils.Interface(abi);
  const referAddress = new URLSearchParams(window.location.search).get("refer");
  const contract = new Contract(MartianSwapMainnet, MartianSwapInterface);
  const [referral, setReferral] = useState(referAddress);
  const { state: addReferalState, send: addReferal } =
    useContractMethod("addReferal");
  const hasReferrer = ifReferrer();
  const referLink = "app.tothemars.in/?refer="+account;
  const { hasCopied, onCopy } = useClipboard(referLink)

  function changeReferral(e) {
    setReferral(e.target.value);
  }

  function pasteReferral() {
    var url_string = window.location.href;
    var url = new URL(url_string);
    var name = url.searchParams.get("refer");
    setReferral(name);
  }

  function calculateBNB(e) {
    let bnb = e.target.value;
    let martian = (bnb - (bnb * 10) / 100) * rate;
    setBnbValue(bnb);
    setMartianValue(martian);
  }

  function bnbMaxValue() {
    if (BNBBalance >= 0.003) {
      let bnb = BNBBalance - 0.0025;
      let martian = (bnb - (bnb * 10) / 100) * rate;
      setBnbValue(bnb.toFixed(8));
      setMartianValue(martian.toFixed(8));
    } else {
      alert("You need at least 0.003 BNB to buy To The Mars (MARTIAN)");
    }
  }

  function calculateMartian(e) {
    let martian = e.target.value;
    let bnb = martian / rate;
    setBnbValue(bnb);

    setMartianValue(martian);
  }

  async function transact() {
    if (
      account &&
      bnbValue >= 0.003 &&
      BNBBalance >= 0.003 &&
      referral != account
    ) {
      try {
        setDisabled(true);
        if (!hasReferrer && referral && referral != account) {
          await addReferal(referral);
        }

        await sendTransaction({
          to: MartianSwapMainnet,
          value: utils.parseEther(bnbValue.toString()),
        });
      } catch (e) {
        console.log(e);
      }
    } else if (
      account &&
      bnbValue >= 0.003 &&
      BNBBalance >= 0.003 &&
      referral == account
    ) {
      toast({
        title: "You can't refer yourself",
        
        description: "You cannot refer yourself. Please copy someone else referal code.",
        status: "error",
        duration: 9000,
        isClosable: true,
      })
    } else {
      toast({
        title: "BNB less than 0.003",
        
        description: "Please buy MARTIAN with alteast 0.003 BNB",
        status: "error",
        duration: 9000,
        isClosable: true,
      })
    }
  }

  function referLinkCopySuccess() {
    onCopy();
    toast({
      title: "Referral link copied successfully.",
      description: "Your referral link is copied successfully. Sent it to someone & let him join the #MartianArmy",
      status: "success",
      duration: 5000,
      isClosable: true,
    });
  }

  useEffect(() => {
    if (state.status != "Mining") {
      setDisabled(false);
      setBnbValue(null);
      setMartianValue(null);
    }
  }, [state]);

  return (
    <VStack
      width="100%"
      height="auto"
      align="center"
      justify="center"
      pt={isPC ? "10vh" : "7vh"}
      pb={isPC ? "6vh" : "7vh"}
    >
      <Box
        width={isPC ? "350px" : "325px"}
        height={isPC ? "410px" : "500px"}
        borderRadius="35px"
        bgColor="teal.400"
        boxShadow="2xl"
        p="20px"
      >
        <HStack p="10px">
          <Text color="white" fontSize="20px">
            Swap
          </Text>
          <Spacer />
          <Box as="button">
            <Icon
              as={BsGear}
              color="white"
              boxSize="20px"
              _hover={{
                color: "orange.400",
              }}
            ></Icon>
          </Box>
        </HStack>
        <Divider />
        <VStack paddingTop="20px" spacing="20px">
          {/* BNB Field */}
          <Box
            w={isPC ? "310px" : "290px"}
            h={isPC ? "100px" : "130px"}
            bgColor="white"
            p="20px"
            borderRadius="30px"
          >
            <HStack color={colorMode == "dark" ? "black" : "black"}>
              <Text>From</Text>
              <Spacer />
              {active && account && chainId == "56" ? (
                <Text>
                  {BNBBalance} <b>BNB</b>
                </Text>
              ) : (
                <Text>-</Text>
              )}
            </HStack>
            <HStack color={colorMode == "dark" ? "black" : "black"}>
              <InputGroup>
                <NumberInput value={!bnbValue ? "" : bnbValue} max={BNBBalance}>
                  <NumberInputField
                    id="BNBField"
                    max={BNBBalance}
                    onChange={calculateBNB}
                    border="0"
                    color="black"
                    w={isPC ? "230px" : "210px"}
                    height="50px"
                    placeholder="0.0"
                    _placeholder={{
                      color: "grey",
                    }}
                    p="0px"
                    _focus={{
                      border: "0px",
                    }}
                  ></NumberInputField>
                  <InputRightElement
                    children={
                      <Box as="button" pt="9px" onClick={bnbMaxValue}>
                        Max
                      </Box>
                    }
                  />
                </NumberInput>
              </InputGroup>

              <Image src={BNBLogo} boxSize="40px" objectFit="cover"></Image>
            </HStack>
          </Box>
          <Box
            w={isPC ? "310px" : "290px"}
            h={isPC ? "100px" : "130px"}
            bgColor="white"
            p="20px"
            borderRadius="30px"
          >
            <HStack color={colorMode == "dark" ? "black" : "black"}>
              <Text>To</Text>
              <Spacer />
              {active && account && chainId == "56" ? (
                <Text>
                  {ToTheMarsBalance} <b>MARTIAN</b>
                </Text>
              ) : (
                <Text>-</Text>
              )}
            </HStack>
            <HStack>
              <InputGroup>
                <NumberInput value={!martianValue ? "" : martianValue}>
                  <NumberInputField
                    id="MartianField"
                    onChange={calculateMartian}
                    border="0"
                    w="200px"
                    color="black"
                    height="50px"
                    placeholder="0.0"
                    _placeholder={{
                      color: "grey",
                    }}
                    p="0px"
                    _focus={{
                      border: "0px",
                    }}
                  ></NumberInputField>
                </NumberInput>
              </InputGroup>
              <Spacer />

              <Image
                src={ToTheMarsLogo}
                boxSize="25px"
                objectFit="cover"
              ></Image>
            </HStack>
          </Box>
          <Button
            width={isPC ? "310px" : "290px"}
            h={isPC ? "55px" : "70px"}
            borderRadius="25px"
            backgroundColor="orange.400"
            onClick={() =>
              sendTransaction({
                to: MartianSwap,
                value: utils.parseEther("1.0"),
              })
            }
            disabled={disabled}
            onClick={active && account ? transact : activateBrowserWallet}
          >
            {active && account
              ? chainId == "56"
                ? "Swap"
                : isPC
                ? "Please switch to BSC Mainnet"
                : "No BSC Network"
              : "Connect Wallet"}
          </Button>
        </VStack>
      </Box>

      <Stack pt="20px" direction={isPC ? "row" : "column"} justify="center" align="center">
        {hasReferrer ? "" :
        <InputGroup size="md">
          <Input
            w="325px"
            
            value={referral}
            onChange={changeReferral}
            placeholder="Paste referral address"
          />
          
        </InputGroup>
        }
        <Box>
        {account && active ? <Button boxShadow="md" variant={colorMode === "light" ? "solid" : "outline"}  onClick={referLinkCopySuccess} bgColor={hasCopied ? "green.300" : ""} _hover>{hasCopied ? <Text>Referral link copied <Icon as={BsCheckAll} /></Text>  :"Copy your referral link"}</Button> : ""}
      </Box>
      </Stack>
      
    </VStack>
  );
}

export default MartianSwap;
