import React, { useState, Fragment } from "react";
import {
  Flex,
  useClipboard,
  VStack,
  HStack,
  useMediaQuery,
  Heading,
  Box,
  Text,
  useToast,
  Icon,
  Image,
  Button,
  useColorMode
} from "@chakra-ui/react";
import { BsCheckAll } from "react-icons/bs";
import QRCode from "react-qr-code";
import MetamaskLogo from "../img/metamask.png";
import TrustwalletLogo from "../img/trustwallet.svg";
import { MartianSwapMainnet } from "../contracts/ContractAddress";

function QRCodePay() {
  const [isPC] = useMediaQuery("(min-width: 750px)");
  const [value, setValue] = useState(MartianSwapMainnet);
  const { hasCopied, onCopy } = useClipboard(value);
  const toast = useToast();
  const {colorMode} = useColorMode();

  function copySuccess() {
    onCopy();
    toast({
      title: "Copied successfully",
      description: "MartianSwap smart contract address copied successfully.",
      status: "success",
      duration: 1000,
      isClosable: true,
    });
  }

  return (
    <VStack
      width="100%"
      h={isPC ? "100vh" : "auto"}
      minWidth={isPC ? "350px" : "290px"}
      alignItems="center"
      pb={isPC ? "25px" : "50px"}
      pt={isPC ? "20px" : ""}
      boxShadow="base"
      
    >
      <Heading align="center" color="twitter.500">
        EVEN MORE EASY WAY TO INVEST
      </Heading>
      <Flex maxWidth="700px" fontSize="lg" textAlign="center" p={isPC ? "" : "10px"}>
        <Box>
          Metamask not installed ? No worry, just scan QR code from your phone's
          crypto wallet or copy the <b>MartianSwap</b> address and send Smart Chain
          BNB's. You will automatically receive <b>To The Mars (MARTIAN)</b> into your
          wallet.
        </Box>
      </Flex>
      <Flex  padding="15px">
        <Box p="20px" borderRadius="10px" border="0.5px" backgroundColor="white" boxShadow="2xl" >
        <QRCode value={MartianSwapMainnet} />
        </Box>
      </Flex>
      <VStack>
        <Text align="center" maxWidth={isPC ? "" : "290px"}>
          {MartianSwapMainnet}
        </Text>
        <Button
          
          onClick={copySuccess}
          variant = {colorMode === "light" ? "solid" : "outline"}
          bgColor={hasCopied ? "green.300" : ""}
          boxShadow="md"
          
          // color={hasCopied ? "black" : "white"}
          pl="10px"
          pr="10px"
          pt="5px"
          pb="5px"
          borderRadius="10px"
        >
          {hasCopied ? (
            <Text>
              MartianSwap Address Copied <Icon as={BsCheckAll} />
            </Text>
          ) : (
            "Copy MartianSwap Address"
          )}
        </Button>
      </VStack>
      <HStack spacing="30px" pt="20px">
        <Image
          objectFit="cover"
          boxSize={isPC ? "50px" : "50px"}
          src={MetamaskLogo}
        />
        <Image
          objectFit="cover"
          boxSize={isPC ? "50px" : "50px"}
          src={TrustwalletLogo}
        />
      </HStack>
    </VStack>
  );
}

export default QRCodePay;
