import React from "react";
import { useColorMode, Flex, IconButton } from "@chakra-ui/react";
import { BsFillBrightnessHighFill, BsMoon } from "react-icons/bs"



function ColorModeChanger() {
    const { colorMode, toggleColorMode } = useColorMode();
    return (
        <Flex position="sticky" align="center" justify="center" height="10vh">
            <IconButton 
                icon={colorMode == "light" ?  <BsMoon/> : <BsFillBrightnessHighFill />} 
                bgColor="transparent" 
                _hover = {{
                    bgColor: "transparent",
                    border: "none"
                }}
                _focus={{
                    border: "none",
                    bgColor: "transparent"
                }}
                _active={{
                    border: "none",
                    bgColor: "transparent"
                }}
                onClick={toggleColorMode} />

        </Flex>

    );
}

export default ColorModeChanger
