import  React from "react";
import {
  Button,
  useMediaQuery,
  Box,
  Text,
  HStack,
  useColorMode,
  Tooltip,
  Image,
  useToast,
  IconButton,
  Icon,
} from "@chakra-ui/react";
import { useEthers } from "@usedapp/core";
import { BsExclamationOctagonFill } from "react-icons/bs";
import { FaCheckCircle } from "react-icons/fa";
import bnbLogo from "../img/bnb.svg";
import { useState } from "react";

function ConnectButton() {
  const { activateBrowserWallet, active, account, chainId, deactivate } =
    useEthers();

  const { colorMode } = useColorMode();
  const toast = useToast();

  const [isPC] = useMediaQuery("(min-width: 950px)");

  async function activateWallet() {
    activateBrowserWallet();
    
  }

  function deactivateWallet() {
    deactivate();
    
  }


  return (
    <>
      {active && account ? (
        <HStack display="flex" alignItems="center" py="0">
          <Box
            as="button"
            display="flex"
            alignItems="center"
            bgColor="transparent"
            borderRadius="xl"
            borderColor={
              colorMode === "light"
                ? chainId == "56"
                  ? "teal.400"
                  : "red.400"
                : chainId == "56"
                ? " white"
                : "red.400"
            }
            borderWidth="1px"
            m="1px"
            px={3}
            height="38px"
          >
            {active && account && chainId == "56" ? (
              <HStack spacing="0px">
                <Text
                  Color={colorMode === "light" ? "black" : "white"}
                  fontSize="md"
                  fontWeight="medium"
                  mr="2"
                >
                  {account &&
                    `${account.slice(0, 8)}
            `}
                </Text>
                <Image src={bnbLogo} boxSize="30px" />{" "}
              </HStack>
            ) : isPC ? (
              "Please switch to BSC Mainnet"
            ) : (
              "No BSC Network !"
            )}

            {chainId == "56" ? (
              <Icon
                as={FaCheckCircle}
                pl="5px"
                boxSize="25px"
                color="green.400"
              />
            ) : (
              <Icon
                as={BsExclamationOctagonFill}
                pl="5px"
                boxSize="23px"
                color="red.400"
              />
            )}
          </Box>
        </HStack>
      ) : (
        <Button
          paddingLeft={isPC ? "20px" : "10px"}
          paddingRight={isPC ? "20px" : "10px"}
          borderRadius="25px"
          backgroundColor="orange.400"
          onClick={activateWallet}
        >
          Connect Wallet
        </Button>
      )}
    </>
  );
}

export default ConnectButton;
