import React from "react";
import ConnectButton from "./ConnectButton";
import { HStack, Spacer, Icon, Image, Box, useMediaQuery, useColorMode } from "@chakra-ui/react";
import { AiFillHome } from "react-icons/ai";
import ColorModeChanger from "./ColorModeChanger";
import logotothemars from "../img/logotothemars.png"

function Nav() {
  const [isPC] = useMediaQuery("(min-width: 750px)")
  const { colorMode, toggleColorMode } = useColorMode();
  return (
    <HStack
      width="100%"
      h="60px"
      paddingLeft="5%"
      paddingRight="5%"
      minWidth="350px"
      zIndex="5"
    
      
      
    >
      <Box as="a" href="https://www.tothemars.in">
      <Image
        src={logotothemars}
        href="www.tothemars.in"
        
        boxSize="50px"
        objectFit="cover"
      ></Image>
      </Box>
      <Spacer />
      <Box as="a" href="https://www.tothemars.in">
      <Icon
        boxSize="30px"
        color="orange.400"
        
        as={AiFillHome}
      />
      </Box>
      <ConnectButton />
      
      <ColorModeChanger/>
    </HStack>
  );
}

export default Nav;
