import { ethers, utils} from "ethers";
import { Contract } from "@ethersproject/contracts";
import {useEthers, useContractCall, useContractFunction } from "@usedapp/core";
import {abi} from "../abi/MartianSwapReferral.json";
import { MartianSwapReferral, DevWallet} from "../ContractAddress";


const MartianSwapInterface = new utils.Interface(abi)
const contract = new Contract(MartianSwapReferral, MartianSwapInterface)




export function Price() {
    const [price] = useContractCall({
      abi: MartianSwapInterface,
      address: MartianSwapReferral,
      method: "price",
      args: [],
    }) ?? [];
    return price;
}

export function GetTime() {
  const [getTime] = useContractCall({
    abi: MartianSwapInterface,
    address: MartianSwapReferral,
    method: "getTime",
    args: [],
  }) ?? [];
  return getTime;
}
export function RemainingToken() {
  const [remain] = useContractCall({
    abi: MartianSwapInterface,
    address: MartianSwapReferral,
    method: "remainingTokens",
    args: [],
  }) ?? [];
  var x = remain/10**18;
  return x;
}

export function TokenAddress() {
  const [tokenAddress] = useContractCall({
    abi: MartianSwapInterface,
    address: MartianSwapReferral,
    method: "token",
    args: [],
  }) ?? [];
  return tokenAddress;
}

export function Owner() {
  const [owner] = useContractCall({
    abi: MartianSwapInterface,
    address: MartianSwapReferral,
    method: "owner",
    args: [],
  }) ?? [];
  return owner;
}

export function ifReferrer() {
  const { activateBrowserWallet, account, chainId, active } = useEthers();
  const [hasReferrer] = useContractCall({
    abi: MartianSwapInterface,
    address: MartianSwapReferral,
    method: "hasReferrer",
    args: [account],
  }) ?? [];
  return hasReferrer;
}

export function useContractMethod(methodName) {
  const {state, send} = useContractFunction(contract, methodName, {})
  return {state, send};
}